.main{
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.container{
    height: auto;
    margin: 0 20px;
}

.dashboard-container {
    max-width: 400px;
    margin: 11px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  label {
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  input {
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  button {
    padding: 10px;
    background-color: #FF9900;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .error-message {
    color: #ff0000;
    margin-top: 10px;
  }
  

  .display{
    height: 74vh;
    width: 70vw;
    background-color: rgb(248, 248, 248);
    margin: 20px auto;
    border: 2px solid black;
    
  }