/* DynamicForm.css */
.main {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 20px;
}

.aws-form-container {
    max-width: 600px;
    /* margin: 20px auto; */
}

.aws-form {
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.aws-form-group {
    margin-bottom: 15px;
}

.aws-form-label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
}

.aws-form-input {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.aws-form-button {
    background-color: #4caf50;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.aws-form-button:hover {
    background-color: #45a049;
}



.right-side {
    height: 81vh;
    width: 50vw;
    overflow: hidden;
    margin-right: 20px;
}

.right-side button {
    margin-top: 2vh;
}