/* Reset some default styles for better consistency */
body, h1, h2, p, button {
    margin: 0;
    padding: 0;
}

/* Global styles */
body {
    font-family: 'Arial', sans-serif;
}

/* Hero section styles */
.hero-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 83vh;
    padding:20px;
    background-color: #ffffff;
    
}

.hero-content {
    flex: 1;
    max-width: 500px;
}

.hero-content h1 {
    font-size: 2em;
    margin-bottom: 20px;
}

.hero-content p {
    margin-bottom: 30px;
    color: #0d0c0c;
}

.hero-image{
    height: 64vh;
    border-radius: 50%;
}

button {
    padding: 10px 20px;
    background-color: #FF9900;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

/* Features section styles */
.features-section {
    display: flex;
    justify-content: space-around;
    margin-bottom: 40px;
}

.feature {
    text-align: center;
    max-width: 300px;
}

.feature h2 {
    font-size: 1.5em;
    margin-bottom: 10px;
}
